import Footer from '~/components/footer.vue'

export default {
  components: {
    Footer
  },

  data() {
    return {
      keyword: ''
    }
  },

  methods: {
    submit() {
      if(this.keyword) {
        const url = '/search?q=' + encodeURIComponent(this.keyword)
        window.location = url
      }
    },

    goLogsRank() {
      window.location = '/log'
    },

    goSpider() {
      window.location = '/spider'
    }
  }
};
